import React from 'react';
import anish from '../../assets/anish.webp';
import prathi from '../../assets/prathi4.png';
import { gsap, TimelineLite } from 'gsap'
import { CSSPlugin } from 'gsap/all'
import './home.scss'

class Home extends React.Component {

    constructor(props){
        super(props);
        this.tl = new TimelineLite(); 
        this.abtme = null;
        this.imgme = null;
        
    }
    componentDidMount(){
        this.tl
        .from(this.abtme, 0.5,{ x: -500,y:-150, autoAlpha: 0 })
        .from(this.imgme,0.5,{ y: 500, autoAlpha: 0 })

        gsap.registerPlugin(CSSPlugin);
    }
        render(){
        return(
            <div className="home" >
            <div className="abt-me" ref={el => this.abtme = el}>
                <p className="textAlign"><span className="headAlign">Hi, I'm Pratheba.</span><br />
                A self-taught designer with 1+ years of designing experience 
                and 2+ of experience in the field of operations.I've also acquired a Google UX professional certification in coursera.
                </p>
            </div>
            <div className="img-me" ref={el => this.imgme = el} >
                <img  className="img1" src={prathi} alt="My Profile" />
            </div>
        </div>
        )
}
}

export default Home;